}

ul {
    margin: 0 0 15px 0;
    padding-left: 15px;
    list-style: square outside none;
}

ol {
    padding-left: 15px;
    margin-bottom: 15px;
}

dfn,
cite,
em,
i {
    font-style: italic;
}

blockquote {
    background-color: #f3fbfd;
    font-size: 18px;
    padding: 25px 70px;
    font-style: italic;
    border-left: 3px solid #229cd6;
    margin: 20px 0;
}

address {
    margin: 0 0 15px;
}

img {
    border: 0;
    max-width: 100%;
    height: auto;
}

a {
    color: #002826;
    background-color: transparent;
    transition: all 0.3s ease;
}

a:hover,
a:focus,
a:active {
    text-decoration: none;
    color: #2caee2;
}

a:focus,
a:hover,
a:active {
    outline: none;
}

