.tm-link-block {
    display: block;
}

.tm-link {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100px;
}

.tm-bg {
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

/*Button tm-Style1*/
.tm-btn {
    background-color: #0058ae;
    border: 1px solid #0058ae;
    color: #fff;
    display: inline-block;
    padding: 10px 20px;
    position: relative;
    opacity: 999;
    border-radius: 4px;
    text-transform: uppercase;
    min-width: 170px;
    text-align: center;
    line-height: 23px;
    cursor: pointer;
}

.tm-btn:focus {
    outline: none;
}

.tm-btn:before,
.tm-btn:after {
    content: '';
    border-style: solid;
    position: absolute;
    z-index: 5;
    border-radius: 3px;
    box-sizing: content-box;
    transition: all 0.4s ease;
}

.tm-btn:before {
    width: 0;
    height: 100%;
    border-width: 1px 0 1px 0;
    top: -1px;
    left: 0;
    transition-delay: 0.05s;
    border-color: #7ab9ff;
}

.tm-btn:after {
    width: 100%;
    height: 0;
    border-width: 0 1px 0 1px;
    top: 0;
    left: -1px;
    border-color: #7ab9ff;
}

.tm-btn:hover:before {
    width: 100%;
}

.tm-btn:hover:after {
    height: 100%;
}

.tm-btn:hover {
    color: #7ab9ff;
    background-color: transparent;
}

.tm-with-border.tm-btn {
    background-color: transparent;
}

.tm-btn-group {
    display: flex;
    flex-wrap: wrap;
}

.tm-btn-group .tm-btn:not(:last-child) {
    margin-right: 20px;
}

.tm-oswald-fonts {
    font-family: 'Oswald', sans-serif;
}

