/*--------------------------------------------------------------
3. Preloader
--------------------------------------------------------------*/
#tm-preloader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 999999;
    background-color: #012244;
    display: flex;
    justify-content: center;
    align-items: center;
}

.tm-preloader-in {
    position: relative;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
}

.tm-preloader-in:before,
.tm-preloader-in:after {
    content: "";
    display: block;
}

.tm-preloader-in .spinner-bar:before,
.tm-preloader-in .spinner-bar:after {
    content: "";
    display: block;
}


@-webkit-keyframes rotation-top {
    0% {
        transform: rotate(0deg);
        opacity: 0.5;
    }

    20% {
        transform: rotate(360deg);
        opacity: 1;
    }

    80% {
        transform: rotate(360deg);
        opacity: 1;
    }

    100% {
        transform: rotate(360deg);
        opacity: 0.5;
    }
}

@keyframes rotation-top {
    0% {
        transform: rotate(0deg);
        opacity: 0.5;
    }

    20% {
        transform: rotate(360deg);
        opacity: 1;
    }

    80% {
        transform: rotate(360deg);
        opacity: 1;
    }

    100% {
        transform: rotate(360deg);
        opacity: 0.5;
    }
}

@-webkit-keyframes rotation-right {
    0% {
        transform: rotate(0deg);
        opacity: 0.5;
    }

    20% {
        transform: rotate(0deg);
        opacity: 0.5;
    }

    40% {
        transform: rotate(360deg);
        opacity: 1;
    }

    80% {
        transform: rotate(360deg);
        opacity: 1;
    }

    100% {
        transform: rotate(360deg);
        opacity: 0.5;
    }
}

@keyframes rotation-right {
    0% {
        transform: rotate(0deg);
        opacity: 0.5;
    }

    20% {
        transform: rotate(0deg);
        opacity: 0.5;
    }

    40% {
        transform: rotate(360deg);
        opacity: 1;
    }

    80% {
        transform: rotate(360deg);
        opacity: 1;
    }

    100% {
        transform: rotate(360deg);
        opacity: 0.5;
    }
}

@-webkit-keyframes rotation-bottom {
    0% {
        transform: rotate(0deg);
        opacity: 0.5;
    }

    40% {
        transform: rotate(0deg);
        opacity: 0.5;
    }

    60% {
        transform: rotate(360deg);
        opacity: 1;
    }

    80% {
        transform: rotate(360deg);
        opacity: 1;
    }

    100% {
        transform: rotate(360deg);
        opacity: 0.5;
    }
}

@keyframes rotation-bottom {
    0% {
        transform: rotate(0deg);
        opacity: 0.5;
    }

    40% {
        transform: rotate(0deg);
        opacity: 0.5;
    }

    60% {
