.tm-preloader-in .bar {
    width: 0;
    height: 0;
    border: 25px solid transparent;
    position: absolute;
    top: 0;
    left: 0;
    transform: rotate(0deg);
    opacity: 0.5;
}

.tm-preloader-in .bar-top {
    border-top: 25px solid #7ab9ff;
    -webkit-animation: rotation-top 3.6s linear 0s infinite;
    animation: rotation-top 3.6s linear 0s infinite;
}

.tm-preloader-in .bar-right {
    border-right: 25px solid #7ab9ff;
    -webkit-animation: rotation-right 3.6s linear 0s infinite;
    animation: rotation-right 3.6s linear 0s infinite;
}

.tm-preloader-in .bar-bottom {
    border-bottom: 25px solid #7ab9ff;
    -webkit-animation: rotation-bottom 3.6s linear 0s infinite;
    animation: rotation-bottom 3.6s linear 0s infinite;
}

.tm-preloader-in .bar-left {
    border-left: 25px solid #7ab9ff;
    -webkit-animation: rotation-left 3.6s linear 0s infinite;
    animation: rotation-left 3.6s linear 0s infinite;
}

/*--------------------------------------------------------------
4. ScrollUp
--------------------------------------------------------------*/
#scrollup {
    position: fixed;
    bottom: -60px;
    right: 25px;
    color: #fff;
    padding: 5px;
    height: 36px;
    width: 36px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 17px;
    cursor: pointer;
    transition: all 0.4s ease;
    z-index: 10;
    background-color: #0058ae;
    box-shadow: 0px 1px 5px 1px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    padding-bottom: 7px;
    text-align: center;
}

#scrollup:hover {
    opacity: 0.8;
}

#scrollup.scrollup-show {
    bottom: 25px;
}

/*--------------------------------------------------------------
5. Site Header
--------------------------------------------------------------*/
.tm-site-header {
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 100;
}

.tm-header-menu {
    height: 100px;
    background-color: #012244;
    transition: all 0.27s cubic-bezier(0.000, 0.000, 0.580, 1.000);
}

.tm-site-header.small-height .tm-header-menu {
    height: 80px;
}

.tm-header-menu>div {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
}

.tm-site-header.small-height {
    transition: all 0.27s cubic-bezier(0.000, 0.000, 0.580, 1.000);
    background-color: #fff;
    top: 0;
}

.tm-language {
    position: relative;
    margin-left: 25px;
}

.tm-active-language {
    color: #fff;
    height: 100%;
    display: block;
    padding: 15px 0;
    cursor: pointer;
    padding-right: 18px;
    position: relative;
}

.tm-active-language:before {
    content: "\F107";
    font-family: 'FontAwesome';
    position: absolute;
    right: -3px;
    top: 50%;
    transform: translateY(-50%);
    transition: all 0.4s ease;
}

.tm-language.tm-active .tm-active-language:before {
    transform: translateY(-50%) rotate(-181deg);
}

.tm-lenguage-dropdown {
    position: absolute;
    width: 100px;
    top: 100%;
    border: 1px solid rgba(122, 185, 255, 0.251);
    background-color: rgba(1, 49, 98, 0.9);
    box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.3);
    padding: 5px 0;
    transform: translateY(10px);
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease;
    border-radius: 2px;
}

.tm-language.tm-active .tm-lenguage-dropdown {
    opacity: 1;
    visibility: visible;
    transform: translateY(0px);
}

.tm-lenguage-dropdown a {
    display: block;
    color: #fff;
    font-size: 14px;
    padding: 3px 10px;
}

.tm-lenguage-dropdown a:hover {
    color: #7ab9ff;
    background-color: rgba(0, 88, 174, 0.251);
}

.tm-menu-meta {
    display: flex;
    margin-left: 20px;
}

.tm-menu-meta .tm-btn {
    min-width: 90px;
    padding: 7px 10px;
    margin-left: 20px;
    line-height: 24px;
}

.tm-menu-meta .tm-btn:first-child {
    margin-left: 0;
}

/*Site Branding*/
.tm-site-branding {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-right: 20px;
}

.tm-logo-link {
    display: inline-block;
    position: relative;
    z-index: 2;
}

.tm-site-title {
    line-height: 30px;
    display: inline-block;
}

.tm-site-title a {
    color: #fff;
    font-weight: 900;
    text-transform: uppercase;
    font-size: 30px;
    display: inherit;
    line-height: inherit;
}

.tm-mobile-logo {
    display: none;
}

/*End Site Branding*/
/*Navigation*/
.tm-primary-nav {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    height: 100%;
}

.tm-primary-nav ul {
    display: flex;
    list-style: none;
    flex-wrap: wrap;
    align-items: center;
    margin: 0;
    padding: 0;
}

.tm-primary-nav .tm-primary-nav-list {
    height: 100%;
}

.tm-primary-nav .menu-item-has-children>ul {
    width: 250px;
    padding-left: 0;
    position: absolute;
    top: 100%;
    display: block;
    z-index: 10;
    opacity: 0;
    visibility: hidden;
    margin-top: 25px;
    transition: all 0.27s cubic-bezier(0.000, 0.000, 0.580, 1.000);
    border: 1px solid rgba(122, 185, 255, 0.251);
    background-color: rgba(1, 49, 98, 0.9);
    box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.3);
    transition-duration: 0.27s;
    border-radius: 2px;
}

.tm-primary-nav .menu-item-has-children:hover>ul {
    opacity: 1;
    visibility: visible;
    margin-top: 0px;
}

.tm-primary-nav .menu-item-has-children>ul>.menu-item ul {
    position: absolute;
    left: 100%;
    right: 100%;
    top: 0;
}

.tm-primary-nav .tm-primary-nav-list .menu-item {
    position: relative;
}

.tm-primary-nav .tm-primary-nav-list>.menu-item {
    margin-right: 20px;
    height: 100%;
}

.tm-primary-nav .tm-primary-nav-list>.menu-item:last-child {
    margin-right: 0px;
}

.tm-primary-nav .menu-item>a {
    font-size: 15px;
    padding: 0;
    display: flex;
    align-items: center;
    color: #fff;
    text-transform: uppercase;
