/*Accordian*/
.accordian-wrapper {
    position: relative;
    margin: auto;
    padding-top: 2px;
    overflow: hidden;
    padding-bottom: 2px;
    margin: 4px 0;
}

.single-accordian:not(:last-child) {
    margin-bottom: 23px;
}

.accordian-head {
    cursor: pointer;
    margin: 0;
    padding-left: 60px;
    position: relative;
    transition: all 0.3s ease;
}

.accordian-head:before {
    content: '';
    position: absolute;
    height: 700px;
    width: 1px;
    background: #fff;
    left: 14px;
    top: -702px;
}

.single-accordian .accordian-body {
    color: #ffffff;
    line-height: 30px;
    font-size: 20px;
    padding: 20px 25px;
    padding-left: 60px;
    padding-bottom: 0;
    padding-top: 23px;
}

.accordian-toggle {
    height: 28px;
    width: 28px;
    border: 1px solid #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    position: absolute;
    left: 0;
    top: 50%;
    margin-top: -14px;
    color: #013162;
    background-color: #013162;
    z-index: 2;
}

.accordian-toggle i {
    height: 16px;
    width: 16px;
    background-color: #fff;
    line-height: 16px;
    text-align: center;
    border-radius: 50%;
    transition: all 0.4s ease;
    font-size: 16px;
}

.accordian-head.active .accordian-toggle i {
    transform: rotate(-180deg);
}

.accordian-head.active .accordian-toggle:after {
    content: "";
    position: absolute;
    width: 14px;
    height: 14px;
    border: 1px solid #fff;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-animation: tm-arrow-animation 2.5s infinite;
    animation: tm-arrow-animation 2.5s infinite;
}

@-webkit-keyframes tm-arrow-animation {
    0% {
        box-shadow: 0 0 0 0 #fff;
    }

    50% {
        box-shadow: 0 0 0 7px #fff;
    }

    100% {
        box-shadow: 0 0 0 0 #fff;
    }
}

@keyframes tm-arrow-animation {
    0% {
        box-shadow: 0 0 0 0 #fff;
    }

    50% {
        box-shadow: 0 0 0 7px #fff;
    }

    100% {
        box-shadow: 0 0 0 0 #fff;
    }
}

