    margin-bottom: 0;
    padding: 12px;
    color: #fff;
    background-color: transparent;
    padding-bottom: 13px;
}

.tm-form-field input:focus,
.tm-form-field textarea:focus {
    outline: none;
}

.tm-form-field label {
    color: #fff;
    font-size: 20px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 0;
    top: 12px;
    margin: 0;
    transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -webkit-transition: all 0.3s ease;
    background: transparent;
}

.tm-form-field input:focus~label,
.tm-form-field input:valid~label,
.tm-form-field textarea:focus~label,
.tm-form-field textarea:valid~label {
    top: -12px;
    left: 0;
    font-size: 15px;
}

.tm-form-field .bar {
    height: 2px;
    width: 0;
    bottom: 1px;
    position: absolute;
    bottom: -1px;
    left: 50%;
    transform: translateX(-50%);
    background: #fff;
    transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -webkit-transition: all 0.4s ease;
}

.tm-form-field input:focus~.bar,
.tm-form-field textarea:focus~.bar {
    width: 100%;
}

