.green-slider-nav .slick-prev.slick-arrow,
.green-slider-nav .slick-next.slick-arrow {
    height: 54px;
    width: 54px;
    background-color: transparent;
    border: none;
    color: transparent;
    overflow: hidden;

    position: absolute;
    left: 50%;
    margin-left: -27px;
    cursor: pointer;
}

.green-slider-nav .slick-prev.slick-arrow {
    top: 0;
}

.green-slider-nav .slick-next.slick-arrow {
    bottom: 0;
