/** button **/

.theme-btn{
  position: relative;
  display: inline-block;
  overflow: hidden;
  font-size: 15px;
  height: 50px;
  padding: 12px 80px 12px 40px;
  background: transparent;
  line-height: 26px;
  color: #4527a4;
  border: 1px solid #4527a4;
  font-weight: 500;
  border-radius: 30px;
  text-transform: capitalize;
  transition: all 500ms ease;
  cursor: pointer;
  z-index: 1;
}

.theme-btn:hover{
  color: #fff;
}

.theme-btn:before {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  height: 100%;
  width: 100%;
  content: "";
  background-color: #4527a4;
  border-radius: 30px;
  transform: scale(0, 1);
  transform-origin: top right;
  transition: transform 500ms cubic-bezier(0.860, 0.000, 0.070, 1.000);
  transition-timing-function: cubic-bezier(0.860, 0.000, 0.070, 1.000);
}

.theme-btn:hover:before {
  transform: scale(1, 1);
  transform-origin: bottom left;
}

.theme-btn i{
  position: absolute;
  top: -1px;
  right: -1px;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  background: #4527a4;
  font-size: 15px;
  color: #fff;
  border-radius: 50%;
  transition: all 900ms ease;
}

.theme-btn:hover i{
  background: transparent;
}

.theme-btn-two{
  position: relative;
