.portfolio-section{
  position: relative;
  padding: 150px 0px 70px 0px;
}

.portfolio-block-one{
  position: relative;
  width: 100%;
  margin-bottom: 30px;
}

.portfolio-block-one .image-box{
  position: relative;
  display: block;
  background: #4527a4;
}

.portfolio-block-one .image-box img{
  position: relative;
  display: block;
  width: 100%;
  transition: all 500ms ease;
}

.portfolio-block-one:hover .image-box img{
  opacity: 0.2;
}

.portfolio-block-one .image-box .content-box {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100%;
  padding: 0px 15px;
  transform: translate(-50%,-50%);
}

.portfolio-block-one .image-box .content-box .inner{
  position: relative;
  text-align: center;
  opacity: 0;
  transform: scale(0.5,0.5);
  transition: all 600ms ease;
  -moz-transition: all 600ms ease;
  -webkit-transition: all 600ms ease;
  -ms-transition: all 600ms ease;
  -o-transition: all 600ms ease;
}

.portfolio-block-one:hover .image-box .content-box .inner{
  opacity: 1;
  transition-delay: 500ms;
  transform: scale(1,1);
}

.portfolio-block-one .image-box .content-box .title{
  position: relative;
  font-size: 18px;
  line-height: 28px;
  color: #fff;
  font-weight: 500;
  margin-bottom: 12px;
}

.portfolio-block-one .image-box .content-box h3{
  position: relative;
  color: #fff;
  font-size: 24px;
  font-weight: 600;
  line-height: 30px;
  margin-bottom: 0px;
  text-transform: uppercase;
}

.portfolio-block-one .image-box .content-box h3 a{
  color: #fff;
}

.portfolio-section .filter-btns li{
  position: relative;
  display: inline-block;
  font-size: 16px;
  line-height: 26px;
  color: #222;
  padding: 8px 35px;
  border-radius: 30px;
  cursor: pointer;
  transition: all 500ms ease;
  text-align: center;
}

.portfolio-section .filter-btns li.active,
.portfolio-section .filter-btns li:hover{
  background: #e5e5e5;
}

.portfolio-section .filter-btns{
  position: relative;
  display: block;
  margin-bottom: 50px;
}


